<template>
  <div>
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" :model="params" ref="filterFormRef">
          <el-form-item label="调出组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @focus="companyListInvok" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="调入组织" size="mini">
            <el-select clearable v-model="params.new_company_id" placeholder="请选择" @focus="companyListInvok" filterable>
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.transfer_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status" placeholder="请选择">
              <el-option label="全部" :value="0">全 部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" label="打印状态">
            <el-select clearable v-model="params.print_times" placeholder="请选择">
              <el-option label="全部" :value="0">全 部</el-option>
              <el-option label="未打印" :value="1">未打印</el-option>
              <el-option label="已打印" :value="2">已打印</el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" @focus="DepartmentListInvok" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="审核日期" size="mini">
            <el-date-picker v-model="timeValue2" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="AddF" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="examineFun(2)" size="mini" v-if="authority.b_pass==1">审核</el-button>
        <el-button plain @click="AUDIT_GET" size="mini" icon="iconfont icon-fanshenhe" v-if="authority.b_nopass==1">弃审</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="examineFun(3)" size="mini" v-if="authority.b_invalid==1">作废</el-button>
      </div>
      <div class="table_container">
        <xTable :ajax='{ url: "getTransferList", params: params }' ref='xTable' :tableColumn='tableColumn' :xData='tableData' />
      </div>
      <publicPrint ref="printDelieveryBill" @Search="onSearch" title="warehouseTransfer" returnParams='info_data' returnTableData='list' ></publicPrint>
    </div>
  </div>
</template>
<script>
import { repertory } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import publicPrint from "common/components/public/PublicPrint.vue"; //公共打印
import { transferware } from "./config/transferwareIndex.js";
export default {
  name: "instorageTransferwareTransferwareIndex",
  mixins: [transferware],
  data() {
    return {
      printFlag: false,
      timeValue2: [],
      dialogFormVisible: false,
      tableHeight: 600
    };
  },
  components: {
    publicPrint
  },
  computed: {
    ...mapGetters(["transferwareIndexGet"])
  },
  methods: {
    ...mapActions([
      "transferwareAddACT",
      "transferwareSeeACT",
      "transferwareModifyACT",
      "transferwareIndexACT"
    ]),
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要弃审的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].transfer_id);
      }
      var rebateArr = addr.toString();
      this.$axMethod
        .v2unAuditorTransfer({
          transfer_id: rebateArr
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg,
              type: "success"
            });
            this.$refs.xTable._getData();
          }
        });
      //弃审
    },
    companyChange(val) {
      this.params.company_name = this.companyList.filter(
        obj => obj.company_id == val
      )[0].company_name;
    },
    seeDetail(scope, row) {
      this.$router.push({
        path: "/inventory/transferware/transferwareSee"
      });
      this.transferwareSeeACT({
        transfer_id: row.transfer_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize,
        after_auditor_print_status: row.after_auditor_print_status
      });
    },
    AddF() {
      this.$router.push({
        path: "/inventory/transferware/transferwareAdd"
      });
      this.transferwareAddACT({
        transfer_id: "",
        params: this.params
      });
    },
    printSheet(row) {
      this.$nextTick(() => {
        this.$refs.printDelieveryBill.printPdf({transfer_id: row.transfer_id});
      });
    },
    // 判断
    judge(row) {
      let company_str = localStorage.getItem("company_str").split(",");
      if (localStorage.getItem("role_id") == 1) {
        return true;
      }
      if (
        company_str.includes(row.company_id) &&
        company_str.includes(row.new_company_id)
      ) {
        return true;
      }
      if (!company_str.includes(row.new_company_id.toString())) {
        this.$message({
          message: "权限不足：暂无调入组织权限",
          type: "warning"
        });
        return false;
      }
      if (!company_str.includes(row.company_id.toString())) {
        this.$message({
          message: "权限不足：暂无调出组织权限",
          type: "warning"
        });
        return false;
      }
      return true;
    },
    modifeF(scope, row) {
      if (!this.judge(row)) {
        return false;
      }
      this.$router.push({
        path: "/inventory/transferware/transferwareAdd"
      });
      this.transferwareAddACT({
        transfer_id: row.transfer_id,
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
    },
    onReset(formName) {
      this.timeValue = [];
      this.timeValue2 = null;
      this.params.print_times = "";
      this.$set(this, "params", {});
      this.$refs.xTable._getData();
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      if (this.timeValue2 && this.timeValue2.length == 2) {
        this.params.auditor_start_time = this.timeValue2[0];
        this.params.auditor_end_time = this.timeValue2[1];
      } else {
        this.params.auditor_start_time = this.params.auditor_end_time = "";
      }
      this.$refs.xTable._getData();
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要操作的信息",
          type: "warning"
        });
        return false;
      }
      var deledata = {
        deleID: "transfer_id",
        deleSelect: this.multipleSelection,
        deleApi: "delTransferBills",
        list: {
          UrlApi: "getTransferList",
          data: this.TableData,
          esData: this.params
        }
      };
      let _this = this;
      this.$common.deletes(deledata, "删除", this, () => {
        _this.$refs.xTable._getData();
      });
    },
    examineFun(status) {
      let vm = this;
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: "请选择需要操作的信息",
          type: "warning"
        });
        return false;
      }
      var addr = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].transfer_id);
      }
      var transfer_id = addr.toString();
      var funcan = {
        examineID: "transfer_id",
        eachData: {
          transfer_id: transfer_id,
          auditor_status: status
        },
        examineApi:
          status == 2
            ? "transferWarehouseAuidtorPass"
            : "transferWarehouseAuditorInvalid",
        list: {}
      };
      let _this = this;
      if (status == 2) {
        // 校验安全库存金额分支
        this.$axMethod
          .safetyStockCheck({
            bill_logogram: "tcyk",
            order_type: 2,
            bill_id: transfer_id
          })
          .then(data => {
            repertory(
              this,
              {
                goods_info: transfer_id,
                status: 2,
                type: 5
              },
              function() {
                vm.$common.examineFun(funcan, vm.onSearch);
              }
            );
          });
      } else {
        this.$common.examineFun(funcan, () => {
          _this.$refs.xTable._getData();
        });
      }
    }
  },
  activated() {
    this.onSearch();
  }
};
</script>

